.wp-block-ncmfse-snap-scroll-pagiantion {
	.wp-block-outermost-icon-block {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 2.5rem;
		height: 2.5rem;
		border-radius: 50%;
		background-color: var(--icon-background-color, transparent);
		color: var(--icon-color, inherit);
		transition-duration: 0.3s;
	}

	&.is-actived,
	&:hover {
		color: var(--active-color, inherit) !important;
		border-color: var(--active-border-color, transparent) !important;
		.wp-block-outermost-icon-block .icon-container {
			background-color: var(
				--active-icon-background-color,
				transparent
			) !important;
			color: var(--active-color, inherit) !important;
			transition-duration: 0.3s;
		}
	}
	&.is-actived {
		svg {
			fill: var(--active-color, inherit);
			color: var(--active-color, inherit);
		}
	}

	.nc__count {
		min-width: 1rem;
		text-align: start;
	}
}
